import UA from 'ua-device';// ua-devicebuild150KBUAJS

class Con {
  constructor(cookieName){
    this.queueData = [];// 
    this.DAY = 86400000; // 
    this.win = window;
    this.doc = document;
    this.scr = window.screen;
    this.nav = navigator;
    this.ua = navigator.userAgent;
    this.protocol = 'https:' === window.location.protocol ? 'https://' : 'http://';
    this.host = 'localhost:9090'; // 
    this.baseUrl = `${this.protocol}${this.host}`;
    this.cookieName = cookieName;// cookie
    this.year = 365;
    this.whiteList = [];
    this.uaOutput = new UA(this.ua);// ua
    this.totalTime = 0;
    this.stayTime = 10000; // 5
    this.compress = true;
  }
  //
  static getInstance(cookieName = 'bp_did') {
    if(!this.instance) {
      this.instance = new Con(cookieName);
    }
    return this.instance;
  }
}

export default Con.getInstance();