// 
class EP {
  constructor(){
    this.appId = '';
    this.appName = '';
  }
  setUp(value = {}){ // key
    for (var i in value) {
      if (Object.prototype.hasOwnProperty.call(value, i) && !Object.prototype.hasOwnProperty.call(this, i)) {
        this[i] = value[i];
      }
    }
  }
}
export default new EP();